body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.sr-only {
  display: none !important;
}

.small-now-playing {
  background-color: white;
  z-index: 500;
  position: fixed;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 90px;
}

.small-now-playing .song-title {
  width: Calc( 85% - 60px );
  padding: 10px;
}
.small-now-playing .play-pause-button {
  display: -ms-flexbox;
  display: flex;
  float: left;
  width: 60px;
}

.play-control {
  /* z-index: 4000; */
  position: fixed;
  bottom: 0px;
  left: 0px;
  width: 100%;
  padding-bottom: 10px;
  height: 165px;
}
.song-title {
  padding: 0px 12px;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
}

.play-control .song-title {
  padding: 0px 12px;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
}
.material-icons {
  width: 30px;
}
.side-icons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}
.now-playing-container {
  display: -ms-flexbox;
  display: flex;
  padding: 30px 10px
}


